.backside {
  display: grid;
  width: 90%;
  margin: auto;
  padding: 4px;
  background-color: white;
  border-radius: 4px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: auto;
  background-color: #2a2a2a;
  .number {
    font-weight: 600;
    color: white;
    text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
      -1px 2px 2px #000;
  }
  .portraitcontainer {
    width: 100%;
    display: grid;

    article {
      display: grid;
      grid-template-columns: 20% 80%;
      .lumaicon {
        width: 50%;
      }
      .statscontainer {
        margin: 0px;
        padding: 0px;
        display: grid;
        font-size: 0.7rem;
        h2 {
          font-size: 0.8rem;
          font-weight: 600;
          color: white;
          text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
            -1px 2px 2px #000;
        }
        li {
          display: grid;
          grid-template-columns: 1fr 1fr;
          font-size: 0.8rem;
          p {
            font-size: 600;
            color: white;
            text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
              -1px 2px 2px #000;
          }
        }
      }
      section {
        padding: 8px;
        color: white;
        border-radius: 4px;
        margin: 8px auto;
        width: 100%;
        gap: 0.5rem;
        text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
          -1px 2px 2px #000;
        font-weight: 600;
        background-color: #2a2a2a;
        .itemscontainer {
          margin: auto;
          width: 50%;
          .lumaicon {
            width: 100%;
          }
          .typecontainer {
            display: grid;
            align-items: center;
            color: white;
            width: 75%;
            border-radius: 4px;
            background-color: #1bd1d4;
            margin: 0.3rem auto;
            grid-template-columns: 1fr 1fr;
            gap: 0.2rem;
            padding: 4px;
            background-color: #2a2a2a;
                div {
                    width: 100%;
                img {
                    width: 50%;
                }
                b {
                    font-size: 0.9rem;
                }
            }
          }
          .weaknesscontainer {
            color: white;
            width: 38vw;
            background-color: #2a2a2a;
            border-radius: 4px;
            .againstcontainer {
                display: grid;
                background-color: #2a2a2a;
                border-radius: 4px;
                margin: auto;
                width: 100%;
                border: none;
              div {
                  display: grid;
                  grid-template-columns: 1fr 1fr;
                  margin-bottom: 0.2rem;
                  margin-top: 0.2rem;    
                  .strongagainst {
                    border-radius: 4px;
                    background-color: #2a2a2a;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    width: 100%;
                    .strongtext {
                      color: limegreen;
                      text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
                        -1px 2px 2px #000;
                      font-weight: 600;
                      font-size: 0.7rem;
                    }
                    img {
                      width: 30px;
                      height: 30px;
                    }
                  }
                  .weakagainst {

                    border-radius: 4px;
                    background-color: #2a2a2a;
                    width: 100%;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    .weaktext {
                      color: tomato;
                      text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
                        -1px 2px 2px #000;
                      font-weight: 600;
                      font-size: 1rem;
                    }
                    img {
                      width: 30px;
                      height: 30px;
                    }
                  }

            }
            }
          }
        }
      }
    }
  }
  
  .evolutionHeader {
    h2 {
      font-size: 1rem;
      font-weight: 600;
    }
    div {
      display: grid;
      grid-template-columns: 0.5fr 1fr 0.5fr;
    }
    p {
      text-align: center;
    }
  }

  .evolutioncontainer {
    display: grid;
    color: white;
    padding: 4px;
    grid-template-columns: 0.5fr 1fr 1fr;
    gap: 4px;
    -webkit-box-shadow: 3px 3px 15px 0px #000000;
    box-shadow: 3px 3px 15px 0px #000000;
    margin-bottom: 0.5rem;
    border-radius: 4px;
    background-color: #1bd1d4;
    text-align: left;
    p {
      margin: 4px;
      color: white;
      color: #fff;
      text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
        -1px 2px 2px #000;
      font-weight: 600;
    }
    b {
      text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
        -1px 2px 2px #000;
      font-weight: 600;
    }
    @media screen and (max-width: 420px) {
      display: grid;
      color: white;
      grid-template-columns: 0.5fr 1fr 0.5fr;
      justify-content: center;
      align-items: left;
      gap: 8px;
      -webkit-box-shadow: 3px 3px 15px 0px #000000;
      box-shadow: 3px 3px 15px 0px #000000;
      margin-bottom: 0.5rem;
      border-radius: 4px;
      background-color: #1bd1d4;
    }
  }
  .techniquecontainer {
    display: grid;
    color: white;
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin-bottom: 4px;
    border: 1px solid #333;
    border-radius: 4px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }
  .traitscontainer {
    display: grid;
    color: white;
    grid-template-columns: 1fr 1fr;
    text-shadow: 1px 2px 2px #000, 0 -1px 2px #000, 0 1px 2px #000,
    -1px 2px 2px #000;
    font-weight: 600;
  }
 
}
